<template>
  <div>
    <div class="container my-3" style="max-width:100%">
      <div class="row">
        <div class="col-md-5" style="max-width:100%">
          <form>
            <div class="form-group d-flex flex-column">
              <div class="d-flex align-center">
                <div>
                  <input
                    type="file"
                    accept="image/*"
                    multiple="multiple"
                    @change="previewMultiImage"
                    class="form-control-file"
                    id="my-file"
                    style="width:300px"
                  />
                  <!-- accept="image/*" --  jpgpnggif  -->
                </div>
                <div
                  class="text-center py-1"
                  style="background-color:black;border-radius:20px;color:white;max-width:150px"
                >
                  <button style="width:100px" @click.prevent="reset"></button>
                </div>
              </div>
              <media :query="{ minWidth: '401px' }">
              <div class="border p-2 mt-3 d-flex">
                <template v-if="preview_list.length">
                  <div v-for="(item, index) in preview_list" :key="index">
                    <img
                      style="width:200px;height:200px"
                      :src="item"
                      class="img-fluid"
                    />
                    <p class="mb-0">: {{ image_list[index].name }}</p>
                  </div>
                </template>
              </div>
              </media>
              <media :query="{ maxWidth: '400px' }">
              <div class="border p-2 mt-3 d-flex flex-column photoside">
                <template v-if="preview_list.length">
                  <div v-for="(item, index) in preview_list" :key="index">
                    <img
                      style="width:200px;height:200px"
                      :src="item"
                      class="img-fluid"
                    />
                    <p class="mb-0">: {{ image_list[index].name }}</p>
                  </div>
                </template>
              </div>
              </media>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Media from 'vue-media'
export default {
  components: {
    Media
  },
  data () {
    return {
      preview: null,
      image: null,
      preview_list: [],
      image_list: []
    }
  },
  methods: {
    previewMultiImage (event) {
      var input = event.target
      var count = input.files.length
      var index = 0
      if (input.files) {
        while (count--) {
          var reader = new FileReader() // FileReader ==> input type='file' file
          reader.onload = e => { // onload ==>  ==>
            this.preview_list.push(e.target.result) // (filereader.result)
            console.log(this.preview_list.length)
          }
          this.image_list.push(input.files[index])
          reader.readAsDataURL(input.files[index])
          index++
        }
      }
      console.log(input.files)
      console.log(reader)
      console.log(this.preview_list.length)
      this.$emit('update', input.files)
      // input.files==>  <input>  DOM  files property FileList
    },
    reset: function () {
      this.image = null
      this.preview = null
      this.image_list = []
      this.preview_list = []
    }
  }
}
</script>
